App.directive('block', function() {
  return {
    restrict: 'E',
    transclude: true,
    scope: {
      title: '=',
      width: '=',
      color: '=',
      borderColor: '='
    },
    templateUrl: 'app/components/block/view.html',
    compile: function(element, attrs){
      if (!attrs.width) { attrs.width = 12; }
      if (!attrs.borderColor) { attrs.borderColor = '\"rgb(255, 255, 255)\"'; }
    }
  };
});